              <li>something</li>
            </ul>
          </div>
        </div>
        <i
          class="bi bi-arrow-right-short"
          :class="{ 'show-right': showRight }"
          @click="slideForward"
        ></i>
      </div>
      <router-link to="/">View all</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "HeaderTwo",
  data() {
    return {
      slider: 100,
      showLeft: true,
      showRight: false,
      counter: 0,
    };
  },
  methods: {
    slideForward(e) {
      this.counter++;
      console.log(this.counter);
      this.slider += 100;
      const slider = e.target.parentElement.querySelector(".container .slider");
      slider.style.left = `-${this.slider}px`;
      console.log(slider.clientWidth);
      if (this.counter >= 9) {
        this.showRight = !this.showRight;
      }
      if (this.counter === 1) {
        this.showLeft = !this.showLeft;
      }
    },
    slideBack(e) {
      this.counter--;
      this.slider -= 100;
      const slider = e.target.parentElement.querySelector(".container .slider");
      slider.style.left = `-${this.slider}px`;
      if (this.counter === 8) {
        this.showRight = !this.showRight;
      }
      if (this.counter <= 1) {
        this.showLeft = !this.showLeft;
      }
    },
  },
};
</script>

