import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import Brand from "../views/Brand";
import Explore from "../views/Explore";
import Blog from "../views/Blog";
import Login from "../views/Login";
import Signup from "../views/Signup";
import Announcement from "../views/blog views/Announcement";
import Product from "../views/blog views/Product";
import Brands from "../views/blog views/Brands";
import Partnership from "../views/blog views/Partnership";
import Community from "../views/blog views/Community";

const routes = [
	{
		path: "/",
		name: "Home",
		component: Home,
	},
	{
		path: "/brand",
		name: "Brand",
		component: Brand,
	},
	{
		path: "/explore",
		name: "Explore",
		component: Explore,
	},
	{
		path: "/blog",
		name: "Blog",
		component: Blog,
	},
	{
		path: "/blog/announcement",
		name: "Announcement",
		component: Announcement,
	},
	{
		path: "/blog/product",
		name: "Product",
		component: Product,
	},
	{
		path: "/blog/brands",
		name: "Brands",
		component: Brands,
	},
	{
		path: "/blog/partnership",
		name: "Partnership",
		component: Partnership,
	},
	{
		path: "/blog/community",
		name: "Community",
		component: Community,
	},
	{
		path: "/login",
		name: "Login",
		component: Login,
	},
	{
		path: "/signup",
		name: "Signup",
		component: Signup,
	},
];

const router = createRouter({
	history: createWebHistory(process.env.BASE_URL),
	routes,
});

export default router;
