    <HeaderOne v-else />
    <HeaderTwo />
    <div class="container">
      <div class="image">
        <img :src="image" alt="" />
      </div>
      <div
        class="close"
        @click="
          () => {
            showScanMenu = false;
            showSearchMenu = false;
          }
        "
      ></div>

      <div class="content">
        <div class="text">
