            @click="
              () => {
                showSearchMenu = !showSearchMenu;
                showScanMenu = false;
              }
            "
          />
          <i
            class="bi bi-upc-scan"
            @click="
              () => {
                showScanMenu = !showScanMenu;
                showSearchMenu = false;
              }
            "
          ></i>
          <SearchMenu v-if="showSearchMenu" />
          <ScanMenu v-if="showScanMenu" />
        </div>
